/*Wadi Pricing Table Widget*/
.wadi-pricing-table-container {
  position: relative;
  overflow: hidden;
  text-align: center;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.wadi-pricing-icon-container {
  -js-display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  line-height: 0;
}

.wadi-pricing-icon-container .wadi-pricing-icon {
  display: inline-block;
}

.wadi-pricing-icon-container .wadi-pricing-image {
  overflow: hidden;
}

.wadi-pricing-icon-container .wadi-pricing-image img {
  width: 25px;
  height: 25px;
  -o-object-fit: cover;
  object-fit: cover;
}

.wadi-badge-left {
  position: absolute;
  top: 0;
}

.wadi-badge-right {
  position: absolute;
  top: 0;
  right: 0;
}

.wadi-badge-left {
  left: 0;
}

.wadi-badge-triangle.wadi-badge-left .corner {
  width: 0;
  height: 0;
  border-top: 150px solid;
  border-bottom: 150px solid transparent;
  border-right: 150px solid transparent;
}

.wadi-badge-triangle.wadi-badge-right .corner {
  width: 0;
  height: 0;
  border-bottom: 150px solid transparent;
  border-right: 150px solid;
  border-left: 150px solid transparent;
}

.wadi-badge-triangle span {
  position: absolute;
  top: 35px;
  width: 100px;
  text-align: center;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  display: block;
  text-transform: uppercase;
}

.wadi-badge-triangle.wadi-badge-right span {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  right: 0;
}

.wadi-badge-circle {
  min-width: 4em;
  min-height: 4em;
  line-height: 4em;
  text-align: center;
  -webkit-border-radius: 100%;
  border-radius: 100%;
  position: absolute;
  z-index: 1;
}

.wadi-badge-stripe {
  position: absolute;
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
  width: 15em;
  overflow: hidden;
  height: 15em;
}

.wadi-badge-stripe.wadi-badge-left {
  -webkit-transform: rotate(0);
  -ms-transform: rotate(0);
  transform: rotate(0);
}

.wadi-badge-stripe .corner {
  text-align: center;
  left: 0;
  -webkit-transform: translateY(-50%) translateX(-50%) translateX(35px) rotate(-45deg);
  -ms-transform: translateY(-50%) translateX(-50%) translateX(35px) rotate(-45deg);
  transform: translateY(-50%) translateX(-50%) translateX(35px) rotate(-45deg);
  margin-top: 35px;
  font-size: 13px;
  line-height: 2;
  font-weight: 800;
  text-transform: uppercase;
}

.wadi-badge-flag .corner {
  text-align: center;
  -webkit-border-radius: 4px 4px 0 4px;
  border-radius: 4px 4px 0 4px;
  padding: 3px 15px;
  position: absolute;
  top: 10%;
  right: 0px;
}

.wadi-badge-flag .corner::before,
.wadi-badge-flag .corner::after {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  top: 100%;
  right: 0;
  border-bottom: 8px solid transparent;
}

.elementor-widget-wadi-addon-pricing-table .elementor-widget-container {
  overflow: visible !important;
}

.wadi-badge-flag .corner::after {
  border-left: 8px solid rgba(0, 0, 0, 0.2);
}

.wadi-pricing-price-currency {
  position: relative;
}

.wadi-pricing-button-container {
  display: block;
}

.wadi-pricing-list {
  -js-display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -moz-box-orient: vertical;
  -moz-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  list-style-type: none;
  margin: 0;
}

.wadi-pricing-list .wadi-pricing-list-item {
  -js-display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.wadi-pricing-list .wadi-pricing-list-item svg {
  width: 50px;
  height: 50px;
}

.wadi-pricing-list .wadi-pricing-list-item img {
  width: 30px;
  height: 30px;
  -o-object-fit: cover;
  object-fit: cover;
}

.wadi-pricing-list .wadi-pricing-list-span {
  position: relative;
}

.wadi-pricing-list .list-item-tooltip {
  border-bottom: 1px dotted;
}

.wadi-pricing-list .wadi-pricing-list-tooltip {
  position: absolute;
  top: -webkit-calc(100% + 1px);
  top: calc(100% + 1px);
  left: 0;
  visibility: hidden;
  padding: 15px 20px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  min-width: 200px;
  overflow: hidden;
  text-align: left;
  font-size: 0.8rem;
  color: #fff;
  background-color: #aaa;
}

.wadi-pricing-features-left .wadi-pricing-list-span {
  text-align: left;
}

.wadi-pricing-features-center .wadi-pricing-list-span {
  text-align: center;
}

.wadi-pricing-features-right .wadi-pricing-list-span {
  text-align: right;
}

.wadi-pricing-list-span:hover .wadi-pricing-list-tooltip {
  z-index: 99;
  visibility: visible;
  opacity: 1;
}

.wadi-pricing-slashed-price-value {
  display: inline-block;
  font-size: 20px;
  font-weight: 400;
  margin-right: 5px;
}

.wadi-pricing-price-value {
  font-size: 70px;
}

.wadi-pricing-description-container li {
  list-style-position: inside;
  text-indent: -40px;
}

@-moz-document url-prefix() {
  .wadi-pricing-description-container li {
    text-indent: 0px;
  }
}
.wadi-pricing-price-button {
  display: block;
  padding: 6px 12px;
  line-height: 1.42857143;
  text-align: center;
  background: #2F45BC;
  color: #fff;
  margin-bottom: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.wadi-pricing-price-button .wadi-pricing-button-text {
  color: #fff;
}